.disc{
    color:white;
    text-align: justify;
    word-spacing: 0.5ch;
    color: #5ddcff;

}
.aboutebox{
    width: 90%;
    margin-top: 30px;
    height: 100vh;
    margin: auto;


}
.aboutebox>h1{
    font-size: 40px;
    color: white;
    animation :aboute 2s linear infinite;
   
}
@keyframes aboute {
    0%{
           text-shadow: 0 0  40px #ff1744,
           0 0  40px  #ff1744,
            0 0  40px #ff1744,
            0 0  40px #ff1744

    }
    50%{
      text-shadow: 0 0  10px #ff1744,
      0 0  5px #ff1744,
       0 0  5px #ff1744,
       0 0  5px #ff1744
    }
    100%{
      text-shadow: 0 0  40px #ff1744,
      0 0  40px  #ff1744,
       0 0  40px #ff1744,
       0 0  40px #ff1744
    }
    
  }
.box{
    margin: auto;
  
   
    width: 45rem;
    border:3px solid orange;
    border-radius: 10px;
   
    padding:30px 30px;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
   
    -webkit-backdrop-filter: blur(10px);
    border-radius: 20px;
    border:1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 #5ddcff; ;     animation-name: moveleft;
    animation-duration: 0.7s;
    margin-top: 25px;
}
@keyframes moveleft {
    0%{
        transform: translateX(1500px);
    }
    100%{
        transform: translate(0);
    }
    
}
.fake{
    border: none;
    width: 200px;
    height:5rem;
    margin: auto;
}

 @media only screen and (max-width: 1200px) {
    .aboutebox{
     width: 50rem;
     height: fit-content;
     padding: 0.5rem;
     margin: auto;
    }
    .aboutebox>h1{
     font-size: 2rem;
 }
    .box{
     width:80%;
     height:fit-content;
     font-size:1rem;
    }
 }


 @media only screen and (max-width: 600px) {
   .aboutebox{
    width: 40rem;
    height: fit-content;
    padding: 0.5rem;
    margin: auto;
   }
   .aboutebox>h1{
    font-size: 2rem;
}
   .box{
    width:80%;
    height:fit-content;
    font-size:1rem;
   }
}