@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@700&family=Dancing+Script&family=Poppins:ital,wght@1,700&display=swap');


.navbarbox{
    border-radius:7px;
    width: 95%;
   left:25px;
    height:45px;
   display: flex;
   align-items: center;
    margin: auto;
    
  
    position: fixed;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(10px);
  
    border:1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
}
.active {
    text-decoration: none;
    color: #ff8f00;
  }
  .default {
    text-decoration: none;
    color: white;
  }
  .navlink{
    width: 60%;
display: flex;
align-items: center;
justify-content: space-around;
  }
  .link{
    transition: 0.2s;
    color: white;
    width: fit-content;
  }
  .link:hover{
    text-shadow: 0 0 10px #ba68c8,
    0 0 20px #ba68c8,
    0 0 20px #ba68c8,
    0 0 20px #ba68c8,
    0 0 20px #ba68c8;

    transform: scale(1.1);
  }
  .logo{
    font-family: 'Cinzel Decorative', cursive;
    /* font-family: 'Dancing Script', cursive; */
    /* font-family: 'Poppins', sans-serif; */
    color: white;
    margin-left: 30px;
  animation :falsh 2s linear infinite;
  }
  @keyframes falsh {
    0%{
           text-shadow: 0 0  40px #03bcf4,
           0 0  40px  #03bcf4,
            0 0  40px #03bcf4,
            0 0  40px #03bcf4

    }
    50%{
      text-shadow: 0 0  10px #03bcf4,
      0 0  5px #03bcf4,
       0 0  5px #03bcf4,
       0 0  5px #03bcf4
    }
    100%{
      text-shadow: 0 0  40px #03bcf4,
      0 0  40px  #03bcf4,
       0 0  40px #03bcf4,
       0 0  40px #03bcf4
    }
    
  }