

.bodybox{

    width: 50rem;
    height:100vh;
    margin: auto;
    margin-top: 20px;
    padding:25px 25px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}


@keyframes moveleft {
  0%{
    opacity: 0;
    transform: translateX(200%);
  }
  100%{
    opacity: 1;
    transform: translate(0);
  }
  
}




  .pic{
    width: 400px;
    height:400px;
    animation-name: moveleft;
    animation-duration: 1s;
  }
  @media only screen and (max-width: 1200px) {
 .bodybox{
padding-top: 4rem;
 }
 .bodybox>div:last-child>img{

  margin-bottom: 5rem;
 }
 
   }
  @media only screen and (max-width: 600px) {
   .bodybox{
    margin: auto;
    width: 30rem;
    margin-left: 4rem;
    height: auto;
   
  
    display: grid;
    grid-template-columns: repeat(1,1fr);
   }
   .bodybox>div:first-child{
    width:90%;
      height: 15rem;
    margin: auto;
    margin-top: 40px;
   }
   .bodybox>div:last-child{
    width:75%;
    height: 20rem;
    margin: auto;
    margin-top: 1rem;
   }
   .bodybox>div:last-child>img{
    width:90%;
    height: 20rem;
   }
 
   .bodybox>div:first-child>div{
    width: 80%;
    margin: auto;
    margin-top: 15px;
   }
   .bodybox>div>img{
    width: 200px;
    height:200px;
    margin: auto;
    margin-right: 6px;
   }

  }